<template>
  <div class="advantage">
    <div class="advantage-title" >
<!--      <h2 class="main-title">专家观点</h2>-->
    </div>

    <div class="advantage-content">

      <div class="advantage-content-right">
      </div>

      <div class="advantage-content-left">
        <div class="content">
          <div class="speak-div"><img src="../assets/speak.svg"></div>
          <div class="content-text">在物流路线的打造过程中，物流企业所担当巨大的责任。合法、专业、稳定、快捷，一定是物流企业的最高追求，这有这样，才能让客户安心、放心、省心，真正服务于用户，这才是我们的的真正价值。
            蓝鲸跨境物流目前正在践行这一理念，从而收获10000+客户的好评。</div>
          <div class="speak-div2"><p></p><img src="../assets/speak.svg"></div>
          <p/>
          <div class="content-person">—— 蓝鲸跨境物流CEO、日本FBA、墨西哥FBA物流专家 陈宇凡</div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: "ExpertPoint"
}
</script>

<style scoped>

.advantage-content .content {
  color: #409eff;
  text-transform: uppercase;
  padding: 10px;
  font-family: 'Microsoft YaHei', 'serif';
  text-align: left;
  margin-bottom: 10px;
}

.advantage-content .content-text {
  line-height: 40px;
  font-size: 20px;
  padding: 40px 0px;
}
.advantage-content .content-person {
  text-transform: uppercase;
  padding: 10px 0px;
  font-weight: bold;
  font-size: 20px;
  text-align: right;
  margin-bottom: 10px;
}

.main-btn {
  display: inline-block;
  position: relative;
  margin: 35px 0px;
  min-width: 115px;
  min-height: 45px;
  outline: none;
  border: 0;
  border-radius: 100px;
  background-color: #409eff;
  color: white;
  cursor: pointer;
  text-align: center;
  line-height: 45px;
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
  font-size: 16px;
}

.advantage-content {
  font-size: 0;
  display: flex;
  flex-flow: row wrap;
  margin-top: 20px;
  padding: 10px;
}

.advantage-content-left {
  width: 100%;
}

.advantage-content-right {
  width: 100%;
  background: url("../assets/leader.png") no-repeat center;
  background-size: contain;
  height: 260px;
}

.advantage-content-left .content {
  font-size: 16px;
  color: #4a4a4a;
  letter-spacing: .5px;
  line-height: 25px;
}

.speak-div {
  width: 52px;
}

.speak-div2 {
  display: flex;
  justify-content: flex-end;
}

.speak-div2 img{
  width: 52px;
  transform: rotate(180deg);

}

/*768*/
@media (min-width: 48em) {
  .advantage-content-left {
    width: 60%;
  }

  .advantage-content-right {
    width: 40%;
    height: 360px;
  }

  .advantage-content .content {
    padding-left: 20px ;
  }
}


/*1024*/
@media (min-width: 64em) {

  .advantage-content-right {
    height: 500px;
  }
 .advantage-content-left {
   padding: 20px;
 }
}

/*1440*/
@media (min-width: 90em) {
  .advantage-content {
    width: 1300px;
    justify-content: center; /* 水平居中 */
    margin-right: auto;
    margin-left: auto;
  }

  .advantage-title .main-title {
    font-size: 42px;
  }
}
</style>



