<template>
  <div>
    <div class="top">
      <img src="../../assets/619869924914b.jpg">
    </div>
    <div class="content">
      <section>
        <article>
          <h1 style="text-align: center; color: #409eff">JCT,你是个啥？</h1>
          <img style="width:100%;" src="../../assets/64a392dce6432.png">
          <span>&nbsp;&nbsp;&nbsp;&nbsp;最近越来越多的客户在咨询JCT到底是什么？我们是否需要注册JCT以及对我们有什么影响。什么是JCT？--日本消费税JCT不是针对于亚马逊卖家，是日本所有满足注册条件的企业都要注册。
<br/>
<span class="specialColor">背景：</span>2023年10月1日前，日本消费税发票制度是比较简易的，商家手写样式不等的发票并盖章，消费者就可以作为类似的发票拿去成本抵扣，所以日本税局为了堵住这个漏洞，决定在今年的10月1日开始实行合格发票留存制度，合格发票可以简单理解为国内的增值税发票，但是日本税局要求必须注册JCT税号，才能开具合格的、可以抵扣的税务发票，发票内容需要会显示出必要的发票内容要素，比如JCT号、品名、金额、国内消费税率、地方消费税率等等，开具完整合格发票后，才可做帐抵扣进项消费税。所以，如果是亚马逊卖家，那么订单客户有需求开具合格发票时，店铺主体没有注册JCT，就开不了合格发票，那会对销售产生很大的影响。
<br/>
   从销售的环节来说，如果目标客人是个人客户，那他们是不需要开票的，因为他们不用纳税申报，不需要发票来抵扣。有卖家担心没有JCT，会影响到商品排名，个人认为，如果商品是销售给个人的，那影响非常有限，但如果是销售给企业，那影响肯定会很大，必须要注册JCT
<br/>
            <span class="boldFont">注册JCT后的利与弊:</span>
<br/>
            <span class="boldFont">优势 :</span>能提高2B订单的竞争力,利于企业的长远经营，卖家可用自己的JCT号，向税局抵扣补或退税
<br/>
            <span class="boldFont">劣势：</span>会增加一定的税务成本
<br/>
注册JCT后，进口需要绑定ACP（海关事务代理人）做逆算申报，用自己的JCT注册主体作为进口商清关。


          </span>
        </article>
      </section>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index"
  }
</script>

<style scoped>
  .top {
    margin-top: 77px;
  }
  .top img {
    width: 100%;
    min-height: 80px;
    object-fit: cover;
  }
  .content {
    padding: 0 2em;
    line-height: 1.8em;
  }
  .specialColor{
    color: red;
  }
  .boldFont {
    font-weight: bold;
  }
  article {
    line-height: 2em;
  }
</style>



