<template>
  <div class="contactus">
    <div class="contactus-title" >
      <h2 class="main-title ta-c">联系我们</h2>
    </div>

    <div class="contactus-content">

      <div class="contactus-content-wrap">
        <div class="contactus-content-detail" style="min-height:260px;">
          <div class="img-box">
            <!-- 公司照片 -->
            <div class="img-box_img address1"></div>
          </div>
          <!-- 公司名称 -->
          <h4 class="contactus-title">深圳总部</h4>
          <div class="info-box">
            <div class="icon-wrap">
              <i class="el-icon-location-information"/>
            </div>
            <div class="info-wrap">
              <p class="addr">深圳市龙岗区坂田街道坂雪岗大道与永香路交汇处创汇国际中心17F</p>
            </div>
            <div class="icon-wrap">
              <i class="el-icon-phone-outline"/>
            </div>
            <div class="info-wrap">
              <p class="phone">13600423522</p>
            </div>
          </div>
        </div>
      </div>


      <div class="contactus-content-wrap">
        <div class="contactus-content-detail" style="min-height:260px;">
          <div class="img-box">
            <!-- 公司照片 -->
            <div class="img-box_img address2" />
          </div>
          <!-- 公司名称 -->
          <h4 class="contactus-title">坂田转运点</h4>
          <div class="info-box">
            <div class="icon-wrap">
              <i class="el-icon-location-information"/>
            </div>
            <div class="info-wrap">
              <p class="addr">深圳市龙岗区坂田街道光雅园绿城科技园C栋1楼</p>
            </div>
            <div class="icon-wrap">
              <i class="el-icon-phone-outline"/>
            </div>
            <div class="info-wrap">
              <p class="phone">13600423522</p>
            </div>
          </div>
        </div>
      </div>

      <div class="contactus-content-wrap">
        <div class="contactus-content-detail" style="min-height:260px;">
          <div class="img-box">
            <!-- 公司照片 -->
            <div class="img-box_img address3" />
          </div>
          <!-- 公司名称 -->
          <h4 class="contactus-title">日本海外仓</h4>
          <div class="info-box">
            <div class="icon-wrap">
              <i class="el-icon-location-information"/>
            </div>
            <div class="info-wrap">
              <p class="addr">日本千葉県船橋市潮見町19-8</p>
            </div>
            <div class="icon-wrap">
              <i class="el-icon-phone-outline"/>
            </div>
            <div class="info-wrap">
              <p class="phone">13600423522</p>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: "ContactUsInfo"
  }
</script>

<style scoped>
  .contactus {
    background-color: #f2f2f2;
  }

  .contactus-title .main-title {
    letter-spacing: 15px;
    color: #409eff;
    text-transform: uppercase;
    padding: 20px 0px;
    font-weight: bold;
    font-size: 28px;
    font-family: 'Microsoft YaHei', 'serif';
    text-align: center;
    margin-bottom: 10px;
    margin-top: 0;
  }

  .contactus-content {
    display: flex;
    flex-wrap: wrap;
    font-size: 0;
    width: 100%;
  }

  .contactus-content-wrap {
    padding-right: 15px;
    padding-left: 15px;
    margin-bottom: 15px;
    width: 100%;
  }

  .contactus-content-detail {
    position: relative;
    width: 100%;
    height: 100%;
    display: inline-block;
    background-color: #fff;
    border-radius: 8px;
    transition: all 0.6s ease-out;
    font-size: 1rem;
  }

  .contactus-content-detail .img-box {
    width: 100%;
    height: 120px;
    overflow: hidden;
    border-right: 3px;
  }

  .contactus-content-detail .img-box .address1 {
    background-image: url("../assets/address1.png");
  }

  .contactus-content-detail .img-box .address2 {
    background-image: url("../assets/address2.png");
  }

  .contactus-content-detail .img-box .address3 {
    background-image: url("../assets/address3.png");
  }

  .contactus-content-detail .img-box .img-box_img {
    transition: .3s ease-in-out;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .contactus-content-detail:hover .img-box .img-box_img {
    /* 鼠标悬停时放大 */
    background-size: 120%;
  }


  .contactus-content-detail .contactus-title {
    font-size: 18px;
    padding: 10px 10px;
    margin: 0;
    color: #051d49;
  }

  .contactus-content-detail .info-box {
    font-size: 0px;
    padding: 0px 10px;
  }

  .contactus-content-detail .info-box .icon-wrap {
    width: 10%;
  }

  .contactus-content-detail .info-box .icon-wrap, .contactus-content-detail .info-box .info-wrap {
    display: inline-block;
    vertical-align: middle;
  }

  .contactus-content-detail .info-box .icon-wrap i {
    font-size: 25px;
  }

  .contactus-content-detail .info-box .info-wrap {
    width: 90%;
    padding-left: 5px;
  }

  .contactus-content-detail .info-box .info-wrap p {
    font-size: 14px;
    word-break: break-all;
    line-height: 1.7;
    margin: 0;
    padding: 3px 0;
  }

  /*768*/
  @media (min-width: 48em) {
    .contactus-content-wrap {
      width: 50%;
    }

    .product-title .main-title {
      font-size: 32px;
    }

    .contactus-content-detail:nth-child(even) {
      /*margin-right: 0;*/
    }
  }

  /*992*/
  @media (min-width: 62em) {
    .contactus-content-wrap {
      width: 33.33%;
    }
  }

  /*1024*/
  @media (min-width: 64em) {

  }

  /*1440*/
  @media (min-width: 90em) {
    .contactus-content {
      width: 1300px;
      justify-content: center; /* 水平居中 */
      margin-right: auto;
      margin-left: auto;
    }

    .contactus-content-detail:nth-child(3n) {
      /*margin-right: 0;*/
    }

    .contactus-title .main-title {
      font-size: 42px;
    }

  }
</style>




