<style scoped>
  .case {
    margin-top: 77px;
  }

  .case-top img {
    width: 100%;
    object-fit: cover;
  }

  .case-top .main-btn {
    display: inline-block;
    position: relative;
    margin: 0px;
    min-width: 115px;
    min-height: 45px;
    outline: none;
    border: 0;
    border-radius: 100px;
    background-color: #409eff;
    color: white;
    cursor: pointer;
    text-align: center;
    line-height: 45px;
    -webkit-transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
    font-size: 16px;
  }

  .case-top {
    height: 200px;
    position: relative;
    background-image: url("../../assets/619869924914b.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .case-top-content {
    position: relative;
    padding: 25px 20px;
    max-width: 1300px;
    margin: 0 auto;
  }

  .case-top-content p {
    font-size: 25px;
    font-weight: bold;
    color: white;
    margin-top: 10px;
  }

  .case-content {
    padding: 5px;
    max-width: 1300px;
    margin: 0 auto;
  }

  .case-content .main-title {
    letter-spacing: 15px;
    color: #409eff;
    text-transform: uppercase;
    padding: 30px 0px 30px 0;
    font-weight: bold;
    font-size: 28px;
    font-family: 'Microsoft YaHei', 'serif';
    text-align: center;
    margin-bottom: 10px;
    margin-top: 0;
  }

  .case-top-bg {
    height: 40vw;
    background-image: linear-gradient(to bottom, #409eff, rgba(239, 242, 252, 0));
    z-index: -1;
  }

  .nolineheight {
    line-height: initial;
  }

  /*576*/
  @media (min-width: 36em) {
    .case-top-content {
      padding: 40px 20px;
    }
  }

  /*768*/
  @media (min-width: 48em) {
    .case-top-content {
      padding: 40px 50px;
    }
  }

  /*1024*/
  @media (min-width: 64em) {
    .case-top-content {
      padding: 40px 100px;
    }
  }

  /*1440*/
  @media (min-width: 90em) {
    .case-top {
      height: 400px;
    }

    .case-top-content {
      padding: 90px 80px;
    }

    .case-top-content p {
      font-size: 50px;
    }

    .case-top .main-btn {
      min-width: 185px;
      min-height: 65px;
      font-size: 28px;
    }
  }

</style>



