<template>
  <div class="option-panel">
    <div class="option-tab">
      <a class="option-btn option-btn-price" rel="nofollow" @click="handelTabSelectPrice">
        <img src="../assets/ic_check_s@2x.png" alt="价格查询">
        <span>价格查询</span>
      </a>
      <a class="option-btn option-btn-track" rel="nofollow" @click="handelTabSelectTrack">
        <img src="../assets/ic_search_s@2x.png" alt="追踪单号">
        <span>追踪单号</span>
      </a>
    </div>

    <div class="option-content" :class="{selectPanelTab: null !== showPriceTable}">
      <div class="option-content-mobile-close"  @click="handelTabSelectNull" > <i class="el-icon-close" ></i> </div>
      <div class="option-content-price" v-if="showPriceTable === true">
        <div class="input-unit-wrap">
          <input type="text" onkeyup="this.value=this.value.replace(/\D/g,'')" placeholder="包裹重量" >
          <div class="unit">
            <select class="unit-option">
              <option value="kg" selected="">kg</option>
              <option value="lbs">lbs</option>
            </select>
          </div>
        </div>

        <div class="package-type-wrap">
          <input v-model="selectPackageType" type="radio" class="PackageType" value="1" id="electricity" >
          <label for="electricity" class="package-type-tab type-electricity" :class="{select: '1' === selectPackageType}">带电</label>
          <input v-model="selectPackageType" type="radio"  value="0" id="goodes">
          <label for="goodes" class="package-type-tab" :class="{select: '0' === selectPackageType}">普货</label>
        </div>

        <div class="input-volume-wrap">
          <input type="text" onkeyup="this.value=this.value.replace(/\D/g,'')" placeholder="长" value="1">
          <span class="" > X </span>
          <input type="text" onkeyup="this.value=this.value.replace(/\D/g,'')" placeholder="宽" value="1">
          <span class="" > X </span>
          <input type="text" onkeyup="this.value=this.value.replace(/\D/g,'')" placeholder="高" value="1">
          <div class="unit">
            <select class="unit-option">
              <option value="cm" selected="">cm</option>
              <option value="inch">inch</option>
            </select>
          </div>
        </div>

        <input type="submit" class="main-btn" value="查询" >
      </div>

      <div class="option-content-track" v-if="showPriceTable === false">
        <textarea cols="10" rows="10" placeholder="请输入运单号"></textarea>
        <input type="submit" class="main-btn" value="查询" >
      </div>

    </div>

  </div>

</template>

<script>
export default {
  name: "OptionPanelTab",
  data() {
    return {
      showPriceTable: null,
      selectPackageType: null,
    }
  },
  created() {
    this.updateDataOnResize();
    window.addEventListener('resize', this.updateDataOnResize);
  },
  destroyed() {
    window.removeEventListener('resize', this.updateDataOnResize);
  },
  methods: {
    updateDataOnResize() {
      const mediaQuery = window.matchMedia('(min-width: 1200px)');
      if(mediaQuery.matches) {
        this.showPriceTable = true;
      }
    },
    handelTabSelectPrice() {
      this.showPriceTable = true;
    },
    handelTabSelectTrack() {
      this.showPriceTable = false;
    },
    handelTabSelectNull() {
      this.showPriceTable = null;
    }
  }
}
</script>

<style scoped>

.option-content{
  width: 100%;
  display: none;
}
.unit {
  display: inline-block;
  vertical-align: middle;
}
.unit-option {
  float: right;
  width: 60px !important;
  border-radius: 5px;
  height: 40px;
  /*background: rgb(9 124 205) !important;*/
  background: #097CCD !important;
  transition: all 0.4s ease;
  padding: 0 4px !important;
  font-size: 14px;
  color: #f5f5f5;
  appearance: auto;
  border: none;
}

.input-unit-wrap input {
  width: 78%;
  border-bottom: 1px solid #fff;
}

input {
  background-color: transparent;
  color: white;
  border: none;
  line-height: 40px;
}

.input-volume-wrap {
  color: white;
  margin-top: 30px;
  margin-bottom: 15px;
}

.input-volume-wrap input {
  width: 20%;
  border-bottom: 1px solid #fff;
}
.input-volume-wrap span {
  opacity: 0.7;
  padding: 0 4px;
  color: white;
}
/**
修改输入框提示文本的颜色
 */
::placeholder {
  color: white;
  opacity: 1; /* Firefox */
}

::-ms-input-placeholder { /* Edge 12 -18 */
  color: white;
}

.option-content-price {
  padding: 80px 20px 20px 20px;
  margin-top: -80px;
  height: 100vh;
  background-color: #409effE5;
}
.package-type-wrap {
  margin-top: 30px;
  border: 1px solid #fff;
  font-size: 0;
}

.package-type-wrap input {
  display: none;
}

.package-type-wrap .type-electricity {
  border-right: 1px solid #fff;
}


.package-type-wrap label {
  color: white;
  display: inline-block;
  width: 50%;
  padding: 13px 0;
  justify-content: center; /* 水平居中 */
  align-items: center;     /* 垂直居中 */
  text-align: center;/* 文本居中 */
  cursor: pointer;
  font-size: 16px;
}
.option-content-mobile-close {
  display: block;
  font-size: 18px;
  height: 80px;
}

.option-content-mobile-close i{
  padding: 10px 20px;
  color: white;
}

.selectPanelTab {
  z-index: 2;
  display: block;
  position: fixed;
  top: 60px;
  left: 0;
  width: 100%;
}

.select {
  background-color: white;
  color: #409eff !important;
}

.option-panel {
  position: inherit;
  width: 100%;
}

.option-tab {
  font-size: 0;
}
.option-tab .option-btn {
  color: white;
  cursor: pointer;
  padding: 15px 19px;
  display: inline-block;
  font-size: 18px;
  width: 50%;
  text-align: center;
}

.option-tab .option-btn span {
  color: white;
}
.option-btn-price {
  background-color: #409effE5;
}
.option-btn-track {
  background-color: rgba(97, 101, 120, 0.9);
}

.option-btn img {
  height: 22px;
  padding-right: 10px;
  vertical-align: bottom;
}


.main-btn {
  display: inline-block;
  position: relative;
  margin: 15px 0px;
  min-width: 115px;
  min-height: 45px;
  outline: none;
  border: 0;
  border-radius: 100px;
  background-color: #409eff;
  cursor: pointer;
  text-align: center;
  line-height: 45px;
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}

.option-content-price  input[type="submit"] {
  background-color: rgba(97, 101, 120, 0.9);
}

.option-content-track {
  padding: 80px 20px 20px 20px;
  margin-top: -80px;
  height: 100vh;
  background-color: rgba(97, 101, 120, 0.9);
}

.option-content-track textarea {
  appearance: none;
  border-radius: 0;
  resize: none;
  width: 100%;
  height: 220px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #fff;
  word-break: normal;
  color: white;
}

/*768*/
@media (min-width: 48em) {
  .contactus-content-wrap {
    width: 50%;
  }
}

/*1024*/
@media (min-width: 64em) {

}

/*1200*/
@media (min-width: 75em) {
  .option-panel {
    position: absolute;
    width: 360px;
    height: 400px;
    left: 65%;
    bottom: 0;
    z-index: 10;
  }
  .option-content {
    display: block;
  }
  .option-content-mobile-close {
    display: none;
  }
  .selectPanelTab {
    z-index: 1;
    display: block;
    position: initial;
    width: 100%;
  }
  .option-content-price{
    height: initial;
    margin-top: 0;
    padding-top: 20px;
  }
  .option-content-track{
    height: initial;
    margin-top: 0;
    padding-top: 20px;
  }
}

/*1440*/
@media (min-width: 90em) {
  .contactus-content {
    width: 1300px;
    justify-content: center; /* 水平居中 */
    margin-right: auto;
    margin-left: auto;
  }

  .contactus-content-wrap {
    width: 33.33%;
    /*margin-right: 0;*/
  }
}
</style>



