<template>
    <header class="header">
      <div class="header__pc">
        <div class="header-logo">
          <img src="../../assets/logo.png">
          <span class="hidden-small">一流的跨境电商物流服务商</span>
        </div>
        <span></span>
        <nav class="menu">
          <ul class="firstnav"  >
            <li class="firstli" v-for="(item , index) in json"  >
              <router-link :to="{path:item.routerUrl}" v-if="!(item.id === 8 || item.id === 9)">
                  <span :class="{title:item.subList.length>0}" >{{item.title}}</span>
              </router-link>
              <a target="_blank" :href="item.routerUrl" v-else>
                <span :class="{title:item.subList.length>0}" >{{item.title}}</span>
              </a>
              <ul class="secdnav" v-show="item.subList.length > 0">
                <li>
                  <i class="sanjiao"></i>
                </li>
                <li v-for="(subItem , index2) in item.subList" >
                  <router-link :to="{path:subItem.routerUrl}">{{subItem.title}}</router-link>
                </li>
              </ul>
            </li>
          </ul>
        </nav>
        <div class="member">
          <a href="https://cn.bing.com" class="member-icon log-in" rel="nofollow">
            <img src="../../assets/login@x2.png" alt="登录">
            <span>登录</span>
          </a>
          <a href="https://www.baidu.com" class="member-icon sign-up" rel="nofollow">
            <img src="../../assets/register2@x2.png" alt="注册">
            <span>注册</span>
          </a>
        </div>
        <span></span>
      </div>

      <div class="header__mobile">
        <div class="header__mobile_logo">
          <div class="header-logo">
            <img src="../../assets/logo.png">
          </div>
          <!--        <div class="menu-toggle"  v-if="noMobileMenuList" >x</div>-->
          <div class="menu-toggle" :class="{ active: !noMobileMenuList }" v-on:click="handleMenu">
            <div class="menu-toggle-icon"></div>
            <div class="menu-toggle-icon"></div>
            <div class="menu-toggle-icon"></div>
          </div>
        </div>

<!--        <i class="el-icon-close" v-on:click="handleMenu" v-else></i>-->
        <nav class="menu" v-show="!noMobileMenuList">
          <ul class="firstnav">
            <li class="firstli"  v-for="(item , index) in json" >
              <span class="title" v-on:click="handelRouter(item)">{{item.title}}</span>
              <span v-if="item.subList.length > 0">
                <i class="el-icon-arrow-up" v-on:click="handleSelect(index, item)" v-if="activeClickIndex === index && item.status"></i>
                <i class="el-icon-arrow-down" v-on:click="handleSelect(index, item)" v-else></i>
              </span>
              <ul class="secdnav" v-show="activeClickIndex === index && item.status">
                <li v-for="(subItem , index2) in item.subList"  @click.stop="handelRouter(subItem)">{{subItem.title}}</li>
              </ul>
            </li>
          </ul>
        </nav>
      </div>
    </header>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      activeClickIndex: -1,
      noMobileMenuList: true,
      secdNavShow: false,
      json: [
        {
          "id": 1,
          "title": "首页",
          "routerUrl": "/",
          "subList": [],
          "status": false
        },
        {
          "id": 2,
          "title": "产品服务",
          "routerUrl": "/product",
          "subList": [
            {
              "id": 21,
              "title": "日本FBA、海外仓",
              "routerUrl": "/product/japan"
            },
            {
              "id": 22,
              "title": "墨西哥FBA",
              "routerUrl": "/product/mexico"
            },
            {
              "id": 23,
              "title": "美国FBA",
              "routerUrl": "/product/usa"
            },
            {
              "id": 21,
              "title": "加拿大FBA",
              "routerUrl": "/product/canada"
            },
            {
              "id": 22,
              "title": "欧洲FBA",
              "routerUrl": "/product/europe"
            },
            {
              "id": 23,
              "title": "澳洲FBA",
              "routerUrl": "/product/australia"
            },
          ],
          "status": false
        },
        {
          "id": 3,
          "title": "客户案例",
          "routerUrl": "/case",
          "subList": [],
          "status": false
        },
        {
          "id": 4,
          "title": "帮助中心",
          "routerUrl": "/help",
          "subList": [],
          "status": false
        },
        {
          "id": 5,
          "title": "关于我们",
          "routerUrl": "/about",
          "subList": [
            {
              "id": 51,
              "title": "企业介绍",
              "routerUrl": "/about"
            },
            {
              "id": 52,
              "title": "企业文化",
              "routerUrl": "/culture"
            },
            {
              "id": 53,
              "title": "合作伙伴",
              "routerUrl": "/partner"
            }
          ],
          "status": false
        },
        {
          "id": 6,
          "title": "联系我们",
          "routerUrl": "/us",
          "subList": [],
          "status": false
        },
        // {
        //   "id": 8,
        //   "title": "员工登录",
        //   "routerUrl": "http://lanjex.nextsls.com/aos/login",
        //   "subList": [],
        //   "status": false
        // },
        // {
        //   "id": 9,
        //   "title": "客户登录",
        //   "routerUrl": "http://lanjex.nextsls.com/wos/login",
        //   "subList": [],
        //   "status": false
        // }
      ]
    }
  },
  methods: {
    handleSelect(index, item) {
      console.log(index, item);
      this.activeClickIndex = index;
      this.secdNavShow = !this.secdNavShow;
      console.log(this.secdNavShow);
      for (var i = 0; i < this.json.length; i++) {
        if (index !== i) {
          this.json[i].status = false;
        }
      }
      this.json[index].status = !this.json[index].status;
    },
    handleMenu() {
      this.noMobileMenuList = !this.noMobileMenuList;
    },
    hanelProduct() {
      this.$router.push("/product")
    },
    handelRouter(item) {
      this.noMobileMenuList = true;
      if (item.id === 8 || item.id === 9) {
        window.open(item.routerUrl, '_blank')
      } else {
        this.$router.push(item.routerUrl)
      }
    }
  }
}
</script>

<style >
  .header-logo span {
    vertical-align: middle;
    font-size: 17px;
    letter-spacing: 5px;
    font-family: 'Microsoft YaHei';
    font-weight: 900;
    font-style: italic;
    text-align: center;
    margin-left: 10px;
  }
  .hidden-small {
    display: none;
  }
.member .member-icon:nth-child(1) img:nth-child(1) {
  width: 26px;
  display: inline;
}
.member .member-icon:nth-child(2) img:nth-child(1) {
  width: 26px;
}
.member img {
  padding-right: 6px;
}
.member .member-icon {
   padding: 8px 16px 10px 16px;
   margin-right: 15px;
   border-radius: 100px;
   background-color: #f2f2f2;
  transition: .3s ease-in-out;
}
.member .member-icon:nth-child(1) {
   background-color: #409eff;
   border: 1px solid #409eff;
}

.member .member-icon:nth-child(2) {
   background-color: #fff;
   border: 1px solid #409eff;
}
.member .member-icon:nth-child(1) span {
  vertical-align: middle;
  font-size: 15px;
  font-weight: 500;
  color: #fff;
}
 .member .member-icon:nth-child(2) span {
  vertical-align: middle;
  font-size: 15px;
  font-weight: 500;
  color: #616578;
}
img {
  vertical-align: middle;
}
.header {
  position: fixed;
  background-color: white;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 20;
}
.header-logo {
  /*width: 200px;*/
  height: 77px;
  display: inline-block;
}
.header-logo > img {
  height: 62px;
  padding: 10px 20px;
  margin-top: 7px;
}

.header__pc {
  display: none;
}

ul , li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header__mobile_logo {
  position: relative;
  width: 100%;
}

.header__mobile {
  width: 100%;
}
.header__mobile .menu{
  background-color: #409eff;
  height: 100vh;
}
.header__mobile .menu span{
  color: white;
}
.menu-toggle {
  width: 50px;
  height: 50px;
  cursor: pointer;
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
  overflow: hidden;
  z-index: 1000;
}

.menu-toggle .menu-toggle-icon {
  width: 65%;
  height: 4px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 2px;
  background-color: #409eff;
  transition: .3s ease-in-out;
  transform-orign: left center;
}

.menu-toggle .menu-toggle-icon:nth-child(1) {
  top: 30%;
}
.menu-toggle .menu-toggle-icon:nth-child(3) {
  top: 70%;
}



.menu-toggle.active .menu-toggle-icon:nth-child(1) {
  width: 70%;
  height: 4px;
  top: 50%;
  transform: translate(-50%) rotate(45deg);
}
.menu-toggle.active .menu-toggle-icon:nth-child(2) {
  -webkit-transform: translateX(-200%);
  -ms-transform: translateX(-200%);
  transform: translateX(-200%);
  opacity: 0.5;
}
.menu-toggle.active .menu-toggle-icon:nth-child(3) {
  width: 70%;
  height: 4px;
  top: 50%;
  -webkit-transform: translate(-50%) rotate(-45deg);
  -ms-transform: translate(-50%) rotate(-45deg);
  transform: translate(-50%) rotate(-45deg);
}

.el-icon-close {
  position: absolute;
  right: 0;
  font-size: 2em;
  line-height: 60px;
  color: grey;
  padding: 0 0.8em 0 calc(50%);
}

.firstnav {
  background-color: #fafafa;
}
.header__mobile .firstnav {
  background-color: transparent;
}
.header__mobile .secdnav li{
  color: white;
}
.firstli {
  padding: 0 0 0 2em ;
  position: relative;
  font-size: 1rem;
  line-height: 3.4rem;
  font-weight: normal;
  /*border-bottom: 1px solid #e5e5e5;*/
  border-bottom: 1px solid rgba(255,255,255,.2);
}

.firstnav i {
  position: absolute;
  right: 0.8em;
  font-size: 1.3em;
  line-height: 1.8em;
  color: white;
  padding: 0.5em 0 0.5em calc(50%);
}

.secdnav .sanjiao {
  position: absolute;
  left: 45%;
  top: -30px;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-bottom-color: #409eff;
  padding: 0;
}


@media (min-width: 64em) {
  .header__mobile{
    display: none;
  }
  .header__pc {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: rgba(0, 0, 0, 0.13) solid 1px;
    /*border-bottom: solid 1px #e6e6e6;*/
  }
  .header-logo {
    margin-right: 1em;
  }
  .menu {
    display: inline-block;
    cursor: pointer;
    margin-right: 1em;
    height: 100%;
    position: inherit;
  }
  .menu ul {
    list-style-type: none;
    margin: 0;
  }
  .menu li {
    list-style-type: none;
    padding: 0 50px;
    line-height: 45px;
    white-space: nowrap;
    font-weight: bold;
  }
  .firstnav {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    background-color: white;
  }
  .firstnav  .firstli {
    float: left;
    padding: 1em 1em;
    position: relative;
    font-size: 16px;
    /*line-height: 2.88rem;*/
    border-bottom: none;
  }
  .menu .secdnav {
    display: none;
  }
  .secdnav {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
    min-width: 7rem;
    border-top: 10px solid #409eff;
    border-radius: 10px;
    text-align: center;
    background: #fff;
    padding: 10px 0;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .5);
    z-index: 2;
    line-height: 1.2rem;
  }

  .secdnav li:hover {
    background: #ededed;
    color: #409eff;
  }
  .secdnav li:hover a {
    background: #ededed;
    color: #409eff;
  }

  .title:hover {
    color: #409eff;
  }

  .firstli:hover ul{
    display: block;
  }

  .firstli:hover span{
    color: #409eff;
  }

  .firstnav::after {
    content: "";
    display: block;
    clear: both;
  }
}

@media (min-width: 90em) {
.firstnav .firstli {
  float: left;
  padding: 10px calc(1vw);
  position: relative;
  font-size: 1em;
  /*line-height: 2.88rem;*/
  border-bottom: none;
}
  .hidden-small {
    display: inline-block;
  }
}

h1, h2, h3, h4, h5, h6, p, a, span, li, label {
  color: #4a4a4a;
  letter-spacing: .5px;
  font-family: "Montserrat", "Microsoft YaHei", "PingFang SC","NotoSansSC", "Microsoft JhengHei", Arial;
  font-weight: normal;
}
</style>



