<template>
  <div class="case">
    <div class="case-top">
      <div class="case-top-content">
        <p>丰富的跨境物流经验，值得信赖</p>
        <input type="submit" class="main-btn" value="在线联系">
      </div>
    </div>
    <div class="case-content">
      <div class="title">
        <h2 class="main-title ta-c">客户案例</h2>
      </div>
      <el-collapse accordion>
        <el-collapse-item>
          <template slot="title"><p class="nolineheight">深圳某大卖使用快船FBA替代空运FBA节省约50%成本</p></template>
          <div>
            深圳某大卖，主营3C电子类产品，之前一直是用传统的空运FBA补货模式，由于疫情期间空运价格不断上涨，再加上日本空运FBA逆算费用非常高昂，后来选择海豚快船FBA方式补货，2020全年时效稳定在7-8个工作日左右，虽然整体比空运多花了1-2个工作日，但是价格只有空运FBA的一半左右，而且2020年统计全年0逆算，在大幅节省成本的同时，有效的保证了正常的稳定运营。
          </div>
        </el-collapse-item>
        <el-collapse-item>
          <template slot="title"><p class="nolineheight">某传统灯具大卖案例</p></template>
          <div>
            广东中山某大卖家，为传统灯具制造工厂，出口日本灯具市场超过15年，其产品主要供应日本各大传统市场，2016年开始自营日本亚马逊店铺，鉴于其产品灯具普遍体积大，泡，且货值较低。该卖家选择大量备货在海豚深圳仓库，由于每周海豚深圳开往日本的船期都有3班以上，时效15天左右，该卖家根据亚马逊FBA库存变动情况，逐批次从海豚深圳仓库小批量海运至FBA仓库，稳定的补货FBA的同时，节省了成本。
          </div>
        </el-collapse-item>
        <el-collapse-item>
          <template slot="title"><p class="nolineheight">3C电子产品</p></template>
          <div>
            山东的客户很在意海外消费者的购物体验，会尽可能的选择一些时效快速的渠道。其次，他在挑选物流商时还看重能否保障货物安全送达的能力，他要实时了解物品运送状态，通关情况，一旦合作下单后，便需要随时和物流商保持密切的联系。
          </div>
        </el-collapse-item>
        <el-collapse-item>
          <template slot="title"><p class="nolineheight">可控 Controllability</p></template>
          <div>用户决策：根据场景可给予用户操作建议或安全提示，但不能代替用户进行决策；</div>
          <div>结果可控：用户可以自由的进行操作，包括撤销、回退和终止当前操作等。</div>
        </el-collapse-item>
      </el-collapse>
    </div>

  </div>
</template>

<script>
  export default {
    name: "index"
  }
</script>

<style scoped>
  .case {
    margin-top: 77px;
  }

  .case-top img {
    width: 100%;
    object-fit: cover;
  }

  .case-top .main-btn {
    display: inline-block;
    position: relative;
    margin: 0px;
    min-width: 115px;
    min-height: 45px;
    outline: none;
    border: 0;
    border-radius: 100px;
    background-color: #409eff;
    color: white;
    cursor: pointer;
    text-align: center;
    line-height: 45px;
    -webkit-transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
    font-size: 16px;
  }

  .case-top {
    height: 200px;
    position: relative;
    background-image: url("../../assets/619869924914b.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .case-top-content {
    position: relative;
    padding: 25px 20px;
    max-width: 1300px;
    margin: 0 auto;
  }

  .case-top-content p {
    font-size: 25px;
    font-weight: bold;
    color: white;
    margin-top: 10px;
  }

  .case-content {
    padding: 5px;
    max-width: 1300px;
    margin: 0 auto;
  }

  .case-content .main-title {
    letter-spacing: 15px;
    color: #409eff;
    text-transform: uppercase;
    padding: 30px 0px 30px 0;
    font-weight: bold;
    font-size: 28px;
    font-family: 'Microsoft YaHei', 'serif';
    text-align: center;
    margin-bottom: 10px;
    margin-top: 0;
  }

  .case-top-bg {
    height: 40vw;
    background-image: linear-gradient(to bottom, #409eff, rgba(239, 242, 252, 0));
    z-index: -1;
  }

  .nolineheight {
    line-height: initial;
  }

  /*576*/
  @media (min-width: 36em) {
    .case-top-content {
      padding: 40px 20px;
    }
  }

  /*768*/
  @media (min-width: 48em) {
    .case-top-content {
      padding: 40px 50px;
    }
  }

  /*1024*/
  @media (min-width: 64em) {
    .case-top-content {
      padding: 40px 100px;
    }
  }

  /*1440*/
  @media (min-width: 90em) {
    .case-top {
      height: 400px;
    }

    .case-top-content {
      padding: 90px 80px;
    }

    .case-top-content p {
      font-size: 50px;
    }

    .case-top .main-btn {
      min-width: 185px;
      min-height: 65px;
      font-size: 28px;
    }
  }

</style>



