<template>
  <div class="customercase">
    <div class="customercase-title" style="">
      <h2 class="main-title ta-c">超过10000+家优秀企业选择了我们</h2>
      <P style="text-align: center;margin-top: 0;">来自各行各业的客户，给了我们高度好评</P>
    </div>

    <div class="customercase-content">
      <div class="customercase-content-up">
        <div class="option-tab">
          <a class="option-btn option-btn-track" :class="{optionBtnPrice : 1 === this.selectIndex}" rel="nofollow" @click="handelTabSelect(1)">
            <i class="el-icon-box"/>
            <span>普货</span>
          </a>
          <a class="option-btn option-btn-track" :class="{optionBtnPrice: 2 === this.selectIndex}"rel="nofollow" @click="handelTabSelect(2)">
            <i class="el-icon-baseball"/>
            <span>玩具</span>
          </a>
          <a class="option-btn option-btn-track" :class="{optionBtnPrice: 3 === this.selectIndex}"rel="nofollow" @click="handelTabSelect(3)">
            <i class="el-icon-goods"/>
            <span>服装</span>
          </a>
          <a class="option-btn option-btn-track" :class="{optionBtnPrice: 4 === this.selectIndex}"rel="nofollow" @click="handelTabSelect(4)">
            <i class="el-icon-camera"/>
            <span>电子</span>
          </a>
        </div>
      </div>

      <div class="customercase-content-down">
        <div class="customercase-content-down-warp" v-show="selectIndex === 1">
            <div class="customercase-content-down-warp-left">
                <img src="../assets/619767ea2e63b.jpg"/>
            </div>
            <div class="customercase-content-down-warp-right">
                <h3>普货类客户</h3>
                <p>和凯琦合作后，我们省了很多心，凯琦帮我们解决了很多物流上的麻烦，像怎么找物流商、怎么挑选适合的路线，凯琦都会给我们列好表格，里面包括时效，签收时间，价格，优劣势等，特别方便，特别轻松简单，至少减轻了我们80%的工作量！</p>
                <input type="submit" class="main-btn" value="了解更多" >
            </div>
        </div>
        <div class="customercase-content-down-warp" v-show="selectIndex === 2">
          <div class="customercase-content-down-warp-left">
            <img src="../assets/toy.png"/>
          </div>
          <div class="customercase-content-down-warp-right">
            <h3>玩具类客户</h3>
            <p>我是朋友介绍我走凯琦的，我们走玩具的也都知道海关查得严，所以在选物流商的时候就特别在意实力这方面，刚开始我也是抱着试一试的心态，测试了几票，发现凯琦的清关这方面确实是比较优秀的，后来也就越来越习惯和凯琦合作了，现在基本上每个月都是在凯琦这里走货。</p>
            <input type="submit" class="main-btn" value="了解更多" >
          </div>
        </div>
        <div class="customercase-content-down-warp" v-show="selectIndex === 3">
          <div class="customercase-content-down-warp-left">
            <img src="../assets/clothes.png"/>
          </div>
          <div class="customercase-content-down-warp-right">
            <h3>服装类客户</h3>
            <p>我们和凯琦合作4年多了，基本上是从我们创业初期就在一起合作了，其实在没与凯琦合作之前，我和很多物流公司都合作过，但是总感觉这些公司不够重视我们这些刚起步的小卖家，经常答应的时效得不到保证，遇到甩柜，我们也只能认栽，后来接触凯琦，发现凯琦的服务真的很用心，经常看到对接我的业务员跑仓库帮我加急出货，我也很感激！</p>
            <input type="submit" class="main-btn" value="了解更多" >
          </div>
        </div>
        <div class="customercase-content-down-warp" v-show="selectIndex === 4">
          <div class="customercase-content-down-warp-left">
            <img src="../assets/photos.png"/>
          </div>
          <div class="customercase-content-down-warp-right">
            <h3>电子产品类客户</h3>
            <p>我们公司是生产3C电子类的产品，货量几十吨的走，货值又很高，所以在出口的时候就需要格外注意，需要直接送到亚马逊仓库，要和物流公司保持密切联系，了解货物的实时动态，因此我们一般不会找一些小货代公司，凯琦在我们这个行业里还是比较有知名度的。</p>
            <input type="submit" class="main-btn" value="了解更多" >
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "CustomerCase",
    data() {
      return {
        msg: 'Welcome to Your Vue.js App',
        activeClickIndex: -1,
        noMobileMenuList: true,
        showPriceTable: true,
        radio3: null,
        selectIndex: 1,
      }
    },
    methods: {
      handelTabSelect(index) {
        this.selectIndex = index;
      },
    }
  }
</script>

<style scoped>

  .customercase{
    background-color: #f2f2f2;
  }

  .customercase-title .main-title {
    /*letter-spacing: 15px;*/
    color: #409eff;
    text-transform: uppercase;
    padding: 30px 0px 10px 0;
    font-weight: bold;
    font-size: 28px;
    font-family: 'Microsoft YaHei', 'serif';
    text-align: center;
    margin-bottom: 10px;
    margin-top: 0;
  }

  .customercase-content {
    max-width: 1300px;
    margin: 0px auto;
    padding: 50px 10px;
  }

  .customercase-content-down-warp {
  }

  .customercase-content-down {
     background: white;
  }

  .customercase-content-down-warp-left {
    display: inline-block;
    width: 100%;
    vertical-align: middle;
  }

  .customercase-content-down-warp-left img{
     width: 100%;
  }
  .customercase-content-down-warp-right {
    width: 100%;
    display: block;
    vertical-align: middle;
    padding-left: 20px;
  }

  .customercase-content-down-warp-right h3 {
    margin-bottom: 20px;
    padding-right: 20px;
    font-size: 30px;
    color: #409eff;
    font-weight: bold;
  }

  .customercase-content-down-warp-right p {
    line-height: 30px;
    padding-right: 30px;
    font-weight: 400;
    text-align: justify;
  }
  .customercase-content-down-warp-right input {
    margin-top: 50px;
  }
  .option-tab {
    font-size: 0;
  }
  .option-tab .option-btn {
    color: white;
    cursor: pointer;
    padding: 15px 0;
    display: inline-block;
    font-size: 18px;
    width: 25%;
    text-align: center;
  }

  .option-tab .option-btn span {
    color: white;
  }
  .optionBtnPrice {
    background-color: #409effE5 !important;
  }
  .option-btn-track {
    background-color: rgba(97, 101, 120, 0.9);
  }

  .option-btn img {
    height: 22px;
    padding-right: 10px;
    vertical-align: bottom;
  }


  .main-btn {
    display: inline-block;
    position: relative;
    margin: 15px 0px;
    min-width: 115px;
    min-height: 45px;
    outline: none;
    border: 0;
    border-radius: 100px;
    background-color: #409eff;
    color: white;
    cursor: pointer;
    text-align: center;
    line-height: 45px;
    -webkit-transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
  }

  /*768*/
  @media (min-width: 48em) {
    .customercase-content-down-warp-left {
      display: inline-block;
      width: 40%;
      vertical-align: middle;
    }

    .customercase-content-down-warp-left img{
      width: 100%;
    }
    .customercase-content-down-warp-right {
      width: 59%;
      display: inline-block;
      vertical-align: middle;
      padding-left: 20px;
    }
  }

  @media (min-width: 90em) {
    .customercase-title .main-title {
      font-size: 42px;
    }
  }

</style>



