import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Product from '@/page/product'
import ProductJapan from '@/page/product/ProductJapan'
import ProductMexico from '@/page/product/ProductMexico'
import Index from '@/page/index'
import About from '@/page/about'
import Culture from '@/page/culture'
import Us from '@/page/us'
import Help from '@/page/help'
import Case from '@/page/case'
import Partner from '@/page/partner'
import Article from '@/page/article'
import ArticleDetail1 from '@/page/article/articleDetail1'
Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    // 始终滚动到顶部
    return { y: 0 }
  },
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/product',
      name: 'Product',
      component: Product
    },
    {
      path: '/product/japan',
      name: 'ProductJapan',
      component: ProductJapan
    },
    {
      path: '/product/mexico',
      name: 'ProductMexico',
      component: ProductMexico
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/culture',
      name: 'Culture',
      component: Culture
    },
    {
      path: '/us',
      name: 'Us',
      component: Us
    },
    {
      path: '/help',
      name: 'Help',
      component: Help
    },
    {
      path: '/case',
      name: 'Case',
      component: Case
    },
    {
      path: '/partner',
      name: 'Partner',
      component: Partner
    },{
      path: '/article/1',
      name: 'ArticleDetail1',
      component: ArticleDetail1
    }
  ]
})



