<template>
  <div class="advantage">
    <div class="advantage-title" >
      <h2 class="main-title ta-c">公司优势</h2>
    </div>

    <div class="advantage-content">
      <div class="advantage-content-left">

        <div class="advantage-content-left-li">
          <div class="item-left">
            <i class="el-icon-lock"></i>
          </div>
          <div class="item-right">
            <h4>安全放心</h4>
            <p>操作规范，仓库监控无死角，保障货物安全运输流畅、流程管理、100%清关</p>
          </div>
        </div>

        <div class="advantage-content-left-li">
          <div class="item-left">
            <i class="el-icon-service"></i>
          </div>
          <div class="item-right">
            <h4>专属客服</h4>
            <p>管家式1对1定制服务，为客户提供贴心的物流服务</p>
          </div>
        </div>

        <div class="advantage-content-left-li">
          <div class="item-left">
            <i class="el-icon-goods"></i>
          </div>
          <div class="item-right">
            <h4>产品多样</h4>
            <p>多渠道多区域，满足市场客户需求，提供高性价比物流产品</p>
          </div>
        </div>

        <div class="advantage-content-left-li">
          <div class="item-left">
            <i class="el-icon-trophy"></i>
          </div>
          <div class="item-right">
            <h4>值得信赖</h4>
            <p>多年国际物流经验，大量忠实客户</p>
          </div>
        </div>

        <div class="advantage-content-left-li">
          <div class="item-left">
            <i class="el-icon-cpu"></i>
          </div>
          <div class="item-right">
            <h4>自研系统</h4>
            <p>自主研发物流系统，提供查看物流进度系统服务，定制物流管理流程，平台流程化管理</p>
          </div>
        </div>

        <div class="advantage-content-left-li">
          <div class="item-left">
            <i class="el-icon-s-promotion"></i>
          </div>
          <div class="item-right">
            <h4>效率快</h4>
            <p>最快速度送达，节省客户时间</p>
          </div>
        </div>

      </div>

      <div class="advantage-content-right">
        <img src="../assets/img11.png" style="width: 90%;">
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "AdvantageInfo"
  }
</script>

<style scoped>

  .advantage-title .main-title {
    letter-spacing: 15px;
    color: #409eff;
    text-transform: uppercase;
    padding: 20px 0px;
    font-weight: bold;
    font-size: 28px;
    font-family: 'Microsoft YaHei', 'serif';
    text-align: center;
    margin-bottom: 10px;
    margin-top: 0;
  }

  .advantage-content {
    font-size: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    text-align: center;
    margin-top: 20px;
  }

  .advantage-content-left {
    width: 100%;
    font-size: 18px;
  }

  .advantage-content-right {
    width: 0;
  }

  .item-left i {
    font-size: 60px;
    color: #409eff;
  }

  .item-left {
    margin-right: 35px;
    display: inline-block;
    vertical-align: middle;
  }

  .item-right {
    display: inline-block;
    width: 65%;
    text-align: left;
    vertical-align: middle;
  }

  .item-right h4 {
    font-size: 24px;
    color: #132644;
    font-weight: bold;
    margin: 0;
  }

  .item-right p {
    font-size: 18px;
    color: #0d2039;
    line-height: 28px;
    margin-bottom: 0;
    margin-top: 5px;
  }

  .advantage-content-left-li {
    margin-bottom: 12px;
  }

  /*768*/
  @media (min-width: 48em) {
  }

  /*1024*/
  @media (min-width: 64em) {
    .advantage-content-left {
      width: 50%;
    }

    .advantage-content-right {
      width: 50%;
    }
  }

  /*1440*/
  @media (min-width: 90em) {
    .advantage-content {
      width: 1300px;
      justify-content: center; /* 水平居中 */
      margin-right: auto;
      margin-left: auto;
    }

    .advantage-title .main-title {
      font-size: 42px;
    }
  }
</style>



