<template>
  <div class="advantage">
    <div class="advantage-title" >
    </div>

    <div class="advantage-content">
      <div class="advantage-content-left">
        <div class="content">
          <h2 class="content-title">日本FBA海运</h2>
          <p><img src="../assets/small_point.png"/> <span>三大联盟船司统配渠道，选择多样</span></p>
          <p><img src="../assets/small_point.png"/> <span>综合运力强，吞载量大，适合大件货</span></p>
          <p><img src="../assets/small_point.png"/> <span>一口整包价，无额外附加费，省事省心</span></p>
        </div>
        <input type="submit" class="main-btn" value="了解更多" >
      </div>

      <div class="advantage-content-right">
<!--        <img src="../assets/img11.png" style="width: 90%;">-->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProductShipment1"
}
</script>

<style scoped>

.advantage-content .content-title {
  color: #409eff;
  text-transform: uppercase;
  padding: 10px 0px;
  font-weight: bold;
  font-size: 28px;
  font-family: 'Microsoft YaHei', 'serif';
  text-align: left;
  margin-bottom: 10px;
}

.main-btn {
  display: inline-block;
  position: relative;
  margin: 35px 0px;
  min-width: 115px;
  min-height: 45px;
  outline: none;
  border: 0;
  border-radius: 100px;
  background-color: #409eff;
  color: white;
  cursor: pointer;
  text-align: center;
  line-height: 45px;
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
  font-size: 16px;
}

.advantage-content {
  font-size: 0;
  display: flex;
  flex-flow: row wrap;
  margin-top: 20px;
  padding: 20px;
}

.advantage-content-left {
  width: 100%;
}

.advantage-content-right {
  width: 100%;
  background: url("../assets/ship.png") no-repeat center;
  background-size: contain;
}

.advantage-content-left .content {
  font-size: 16px;
  text-align: left;
  color: #4a4a4a;
  letter-spacing: .5px;
  line-height: 25px;
}

/*768*/
@media (min-width: 48em) {
}

/*1024*/
@media (min-width: 64em) {
  .advantage-content-left {
    width: 50%;
  }

  .advantage-content-right {
    width: 50%;
  }
}

/*1440*/
@media (min-width: 90em) {
  .advantage-content {
    width: 1300px;
    justify-content: center; /* 水平居中 */
    margin-right: auto;
    margin-left: auto;
  }

  .advantage-title .main-title {
    font-size: 42px;
  }
}
</style>



